type SlotKey = 'morning' | 'afternoon';
type SectionKey = 'main' | 'dessert';

interface SlotContent {
  main: string[];
  dessert: string[];
}

interface DayColumn {
  id: string;
  day: GridDay;
  themeLabel: string;
  accentColor: string;
  capacity: number;
  price: number;
  slots: Record<SlotKey, SlotContent>;
}

interface SmallEvent {
  id: string;
  start: string;
  end: string;
  dishes: string[];
}

interface GeneratedCourse {
  id: string;
  class_date: string;
  session: string;
  start_time: string;
  end_time: string;
  capacity: number;
  price: number;
  detail: string;
  tag?: string;
}

interface SelectedCell {
  columnId: string;
  slotKey: SlotKey;
  section: SectionKey;
}

type MenuMap = Map<string, MenuItem>;

